import React from "react";
import Helmet from "react-helmet";
import { graphql } from "gatsby";
import Layout from "../layout";
import UserInfo from "../components/UserInfo/UserInfo";
import Disqus from "../components/Disqus/Disqus";
import {PostListTags} from "../components/Tags/Tags";
import SocialLinks from "../components/SocialLinks/SocialLinks";
import SEO from "../components/SEO/SEO";
import config from "../../data/SiteConfig";
import "./b16-tomorrow-dark.css";
import "./post.css";
import Valine from 'valine';
const APP_ID = 'agYKv9ldJMM4QKMoA5cKc9df-MdYXbMMI'
const APP_KEY = '0D9g85YcAg15lEiS8yme5JfL'


export default class PostTemplate extends React.Component {
  constructor(...args){
    super(...args)

    // if (typeof Valine!='undefined')
  }

  componentDidMount() {
      new Valine({
        el: '#post_comments',
        appId: APP_ID,
        appKey: APP_KEY,
        recordIP:true,
        placeholder:'Comment me, my friend!'
      })
  }

  render() {
    const { data, pageContext } = this.props;
    const { slug } = pageContext;
    const postNode = data.markdownRemark;
    const post = postNode.frontmatter;
    if (!post.id) {
      post.id = slug;
    }
    if (!post.category_id) {
      post.category_id = config.postDefaultCategoryID;
    }
    return (
      <Layout>
        <script src='//unpkg.com/valine/dist/Valine.min.js'></script>
        <div className='post-all'>
          <Helmet>
            <title>{`${post.title} | ${config.siteTitle}`}</title>
          </Helmet>
          <SEO postPath={slug} postNode={postNode} postSEO />
          <div>

            <h1>{post.title}
              <div className="post-tags">
                <PostListTags draggable={false} tags={post.tags} hasLink={true} tagItemClass={{
                  opacity:1
                }} />
              </div>
            </h1>
            <div dangerouslySetInnerHTML={{ __html: postNode.html }} />
            <div className="post-meta">
              <div id="post_comments"></div>
              <SocialLinks postPath={slug} postNode={postNode} />
            </div>
            <UserInfo config={config} />



            {/*<Disqus postNode={postNode} />*/}
          </div>
        </div>
      </Layout>
    );
  }
}

/* eslint no-undef: "off" */
export const pageQuery = graphql`
  query BlogPostBySlug($slug: String!) {
    markdownRemark(fields: { slug: { eq: $slug } }) {
      html
      timeToRead
      excerpt
      frontmatter {
        title
        cover
        date
        category
        tags
      }
      fields {
        slug
        date
      }
    }
  }
`;
